/*==============================
	Common styles
==============================*/
html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

html,
body {
  height: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  /* font-family: 'Inter' !important; */
  font-family: "Josefin Sans", sans-serif !important;
}

body {
  letter-spacing: 0px;
  /* font-family: 'Inter' !important; */
  font-family: "Josefin Sans", sans-serif !important;
  font-weight: 400;
  background-image: linear-gradient(180deg, #ffedf7, #fff) !important;
  background-repeat: no-repeat;
  background-attachment: fixed;
  /* background-color: #fff !important; */
  color: #27346d !important;
  -webkit-font-smoothing: antialiased;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
  transition: 0.5s ease;
  transition-property: color, background-color, border-color, box-shadow;
  cursor: pointer;
}

button:focus {
  outline: none;
}

a {
  transition: 0.5s ease;
  transition-property: color, background-color, border-color, box-shadow;
}

a:hover,
a:active,
a:focus {
  outline: none;
  text-decoration: none !important;
}

input,
textarea,
select {
  padding: 0;
  margin: 0;
  border-radius: 0;
  -webkit-appearance: none;
  appearance: none;
  box-shadow: none;
  transition: 0.5s ease;
  transition-property: background-color, border-color;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

select::-ms-expand {
  display: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

::selection {
  background: #aa72ce;
  color: #141a2a;
  text-shadow: none;
}

::-webkit-input-placeholder {
  color: #dedede;
  opacity: 1;
}

::-moz-placeholder {
  color: #dedede;
  opacity: 1;
}

:-moz-placeholder {
  color: #dedede;
  opacity: 1;
}

:-ms-input-placeholder {
  color: #dedede;
  opacity: 1;
}

:focus {
  outline: -webkit-focus-ring-color auto 0px;
}

:focus-visible {
  outline: 0px dotted;
}

.tab-content {
  width: 100%;
}

.tab-content>.tab-pane {
  display: none;
}

.tab-content>.active {
  display: block;
}

.fade {
  transition: opacity 0.4s linear;
}

@media screen and (prefers-reduced-motion: reduce) {
  .fade {
    transition: none;
  }
}

.fade:not(.show) {
  opacity: 0;
}

.collapse:not(.show) {
  display: none;
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease;
}

@media screen and (prefers-reduced-motion: reduce) {
  .collapsing {
    transition: none;
  }
}

.row--grid {
  margin-right: -10px;
