/*==============================
	Header
==============================*/
.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 99;
  background-color: transparent;
  transition: background-color 0.5s ease;
  /* background-color: #100c3d; */
  background-color: transparent;
  border-bottom: 1px solid rgba(170, 114, 206, 0.12);

}

.header--active .header__logo {
  pointer-events: auto;
}

.header__content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
  height: 80px;
  width: 100%;
}

.header__logo {
  z-index: 1;
  width: auto;
  height: 50px;
  display: inline-flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  padding-left: 10px 28px;
  pointer-events: none;
  margin-right: 5px;
  /* filter: drop-shadow(5px 5px 5px #777); */
}

.header__logo img {
  height: 57px;
  width: 60px;
}

.header__btn {
  position: absolute;
  width: 24px;
  height: 22px;
  display: block;
  left: 0;
}

.header__btn span {
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #fff;
  border-radius: 3px;
  transition: 0.5s ease;
  opacity: 1;
}

.header__btn span:first-child {
  top: 0;
}

.header__btn span:nth-child(2) {
  top: 10px;
  width: 16px;
}

.header__btn span:last-child {
  top: 20px;
  width: 8px;
}

.header__btn--active span:first-child {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  top: 10px;
}

.header__btn--active span:nth-child(2) {
  opacity: 0;
}

.header__btn--active span:last-child {
  width: 24px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  top: 10px;
}

.header__tagline {
  display: none;
}

.header__nav {
  margin-top: 16px;
  position: fixed;
  width: 250px;
  top: 80px;
  bottom: 0;
  left: 0;
  /* background-color: #141a2a; */
  padding: 30px 15px 0;
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
  -webkit-transform: translate3d(-250px, 0, 0);
          transform: translate3d(-250px, 0, 0);
  border-top: 1px solid rgba(170, 114, 206, 0.1);
}

.header__nav--active {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);

}

.header__nav li {
  display: block;
  margin-bottom: 25px;
}

.header__nav li:last-child {
  margin-bottom: 0;
}

.header__nav a {
  display: inline-block;
  font-size: 14px;
  line-height: 24px;
  font-weight: 600;
  color: #5D6785 !important;
  text-transform: uppercase;
}

.header__nav a svg {
  fill: #fff;
