.team {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  position: relative;
  margin-top: 20px;
}

.team:hover .team__social a {
  opacity: 1;
  margin-top: 0;
}

.team:hover .team__img:before {
  opacity: 1;
}

.team__img {
  border-radius: 20px;
  width: 100%;
  overflow: hidden;
  z-index: 2;
  position: relative;
}

.team__img img {
  position: relative;
  width: 100%;
  z-index: 1;
}

.team__img:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: linear-gradient(0deg, rgba(29, 38, 59, 0.85) 0%, rgba(29, 38, 59, 0.1) 100%);
  z-index: 2;
  opacity: 0;
  transition: opacity 0.5s ease;
}

.team__social {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  position: absolute;
  left: 15px;
  right: 15px;
  bottom: 20px;
  height: 20px;
  z-index: 3;
}

.team__social a {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-right: 15px;
  opacity: 0;
  margin-top: 20px;
  transition: 0.5s ease;
  transition-property: opacity, margin-top;
}

.team__social a:first-child {
  transition-delay: 50ms;
}

.team__social a:nth-child(2) {
  transition-delay: 100ms;
}

.team__social a:nth-child(3) {
  transition-delay: 150ms;
}

.team__social a:nth-child(4) {
  transition-delay: 200ms;
}

.team__social a:nth-child(5) {
  transition-delay: 250ms;
}

.team__social a:nth-child(6) {
  transition-delay: 300ms;
}

.team__social a:nth-child(7) {
  transition-delay: 350ms;
}

.team__social a svg {
  width: 20px;
  height: auto;
  fill: #fff;
  transition: fill 0.5s ease;
}

.team__social a:last-child {
  margin-right: 0;
}

.team__social a:nth-child(3n+1):hover svg {
  fill: #aa72ce;
}

.team__social a:nth-child(3n+2):hover svg {
  fill: #e8d189;
}

.team__social a:nth-child(3n+3):hover svg {
  fill: #227f9e;
}

.team__about {
  z-index: 1;
