.roadmap {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  position: relative;
}

.roadmap:before,
.roadmap:after {
  content: '';
  position: absolute;
  display: block;
  border-radius: 50%;
}

.roadmap:before {
  width: 24px;
  height: 24px;
  left: 0;
  top: 42px;
  background-color: #fff;
  opacity: 0.12;
  z-index: 1;
}

.roadmap:after {
  width: 10px;
  height: 10px;
  left: 7px;
  top: 49px;
  background-color: #fff;
  z-index: 2;
}

.roadmap--active:before,
.roadmap--active:after {
  background-color: #aa72ce;
}

.roadmap--active:after {
  width: 16px;
  height: 16px;
  left: 4px;
  top: 46px;
}

