body {
    background-color: #10170e !important;
    margin: 0;
    padding: 0;
    position: relative;
    font-family: Inter !important;
}

.App {
    display: inline-table;
    position: relative;
    width: 100%;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type=number] {
    -moz-appearance:textfield; /* Firefox */
}

.logo {
    color: #53c351 !important;
    cursor: pointer;
    font-family: Inter;
    font-size: 36px !important;
    font-style: normal;
    font-weight: 590;
    line-height: 150%;
    text-decoration: none;
    text-transform: uppercase;
    transition: .3s;
}

.logo:hover {
    color: #0cf508 !important;
    font-weight: 610 !important;
}

.nav-text {
    color: white !important;
    cursor: pointer;
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 590;
    line-height: 140%;
    text-decoration: none;
    text-transform: uppercase;
    transition: .3s;
    margin-right: 15px;
}

.nav-text:hover {
    color: rgb(218, 180, 180) !important;

}

.connectWallet {
    background: #53c351;
    border: 1px solid #53c351;
    border-radius: 18px;
    color: #fff;
    width: 188.22px;
    padding: 14px 20px;
    font-size: 12px;
    text-transform: uppercase;
}
.connectWallet2 {
    background: #53c351;
    border: 1px solid #53c351;
    border-radius: 18px;
    color: #fff;
    width: 188.22px;
    padding: 0px 20px;
    font-size: 12px;
    text-transform: uppercase;
}

.connectWallet:hover {
    background-color: #283825;
    border: 1px solid #53c351;
    color: #53c351;
}

.justify-content-right {
    justify-content: right;
}
.nav-right {
    align-items: center;
    display: flex;
    gap: 10px;
}

.nav-twitter {
    padding-left: 13px !important;
    color: white !important;
    align-items: center;
    background: #3d4c3b;
    border-radius: 50%;
    width: 40px !important;
    cursor: pointer;
    display: flex;
    transition: .3s;
}


.dropdown {
    position: relative;
    display: inline-block;
}
  
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    padding: 12px 16px;
    z-index: 1;
}
  
.dropdown:hover .dropdown-content {
    display: block;
}

.dropdown-content {
    bottom: 0;
    color: #fff;
    font-family: Inter;
    background: #283825;
    border-radius: 5px;
    padding: 10px;
    width: 100%;
}

.dropdown-content a {
    color: #fff;
}

.mobile-menu .mobile-contact p {
    border-bottom: 1px solid #ebebeb;
    padding: 0px !important;
}

.dropdown-content li {
    list-style: none
}

.nav-twitter:hover {
    background: #849283;
}

.nav-send:hover {
    background: #849283;
}

.nav-user:hover {
    background: #849283;
}

.nav-send {
    padding-left: 11px !important;
    color: white !important;
    align-items: center;
    background: #3d4c3b;
    border-radius: 50%;
    width: 40px !important;
    cursor: pointer;
    display: flex;
    transition: .3s;
}


.nav-user {
    padding-left: 9.5px !important;
    color: white !important;
    align-items: center;
    background: #3d4c3b;
    border-radius: 50%;
    width: 40px !important;
    cursor: pointer;
    display: flex;
    transition: .3s;
}

.staking-body {
    background-color: #283825;
    border-radius: 25px;
    display: flex;
    flex-direction: column;
    gap: 25px;
    margin: 0 auto;
    padding: 30px 40px;
    width: 400px;
}

.staking-body .info span {
    font-family: Inter;
    font-size: 15px;
    line-height: 30px;
    margin: 0;
    text-transform: uppercase;
    color: white;
}

.input {
    color: #fff;
    outline: none;
    transition: .3s;
    width: 100%;
}

.hire-bnb-input {
    background-color: hsla(0,0%,100%,0);
    border: 1px solid #92c589;
    border-radius: 15px;
    color: #fff;
    font-family: Inter;
    font-size: 15px;
    padding: 10px 145px 10px 10px !important;
    text-align: center;
    width: calc(100% - 157px);
}

.staking {
    margin-top: -10px;
