    height: 10px;
}

.mobile-menu {
    bottom: 0;
    display: none;
    position: fixed;
    width: 100%;
    z-index: 999999;
}

@media screen and (max-width: 768px) {
    .mobile-menu {
        background-color: #283825;
        display: block !important;
    }
}

@media screen and (max-width: 768px) {
    .mobile-menu .body {
        align-items: center;
        display: flex;
        flex-direction: row;
        gap: 35px;
        justify-content: center;
        padding-bottom: 40px;
        padding-top:5px;
    }
}

.mobile-menu .mobile-contact .contact {
    position: relative;
}

.mobile-menu .mobile-contact .contact div {
    background-color: #567151;
    border-radius: 5px;
    display: block;
    opacity: 0;
    position: absolute;
    right: -38px;
    top: -120px;
    visibility: hidden;
    width: 100px;
}

.mobile-menu .mobile-contact .contact {
    position: relative;
}

.mobile-contact button {
    align-items: center;
    background-color: hsla(0,0%,100%,0);
    border: 0;
    display: flex;
    flex-direction: column;
    opacity: .3;
    position: relative;
}
.mobile-menu .mobile-contact .contact div ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.mobile-menu .mobile-contact .contact div ul li {
    display: flex;
    text-align: left;
}

.mobile-menu .mobile-contact .contact div ul li a {
    color: #fff;
    font-family: Inter;
    padding: 15px;
    width: 100%;
}

.mobile-menu .mobile-contact .contact div ul p {
    border-bottom: 1px solid #ebebeb;
    margin: 0;
}

.mobile-contact button img {
    height: 20px;
    width: 20px;
}

@media screen and (max-width: 768px) {
    .staking-body {
        width: 350px !important;
    }

    .referal-body {
        width: 350px !important;
    }

    .mobile-header {
        display: block !important;
    }
    .home .image {
        display: none !important;
    }
}

